<template>
  <v-container class="">
    <v-layout
      justify-center
      style="height: 600px"
      align-center
      class="white--text"
    >
      <v-flex class="" xs12 md6>
        <v-card :loading="carga" color="rgba(0,0,0,0.3)" class="white--text">
          <v-card-title>
            <v-layout justify-center>
