                </p>
              </v-layout>
              <v-layout>
                <v-otp-input
                  v-model="usuario"
                  length="7"
                  type="number"
                  dark
                ></v-otp-input>
              </v-layout>
              <v-layout>
                <v-text-field
                  prepend-icon="mdi-lock"
                  label="Contrase√±a"
