<script>
import axios from "axios";
import { mapMutations , mapState} from "vuex";
import Carga from "../components/Carga.vue";

export default {
  components: { Carga },
  state: {},
  created() {},
  props: {},
  data() {
    return {
      usuario: "",
      contrase√±a: "",
      show1: false,
      carga: false,
      con: false,
      //DATOS
      Info: "",
      Espera: true,
      Correcto: false,
      information:"",
      error:false
    };
  },
  watch: {},
  methods: {
    ...mapMutations(["Modificar_cargar", "Modificar_login"]),

    Iniciar() {
      this.carga = !this.carga;
    },
    async modifyPost() {
      this.Modificar_cargar(true);
      this.Info =  "Comprobando Usuario Y Contrase√±a"
      this.information='';
      this.Espera=true;
      this.error = false;      
      const json = JSON.stringify({
        No_Trabajador: this.usuario,
        password: this.contrase√±a,
      });
      
        let res = await axios.post("http://localhost:3000/login", json, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "application/json",
          },
        }).catch(error =>{
          this.Espera = false;
          this.Correcto=false;  
          this.error=true;
          if(error.response.status == 400){
            this.information=error.response.data.error;            
          }
        })
        if(!this.error){
          this.Espera = false;
          this.Correcto=true;
          this.information="Bienvenido "+ res.data.Nombre;  
          this.Modificar_login(true);                      
        }
           
    },
  },
  computed:{
      ...mapState(['login']),
      termino(){
        let u = ""
        u.length
        return this.usuario.length == 7 && this.contrase√±a != ""
      }
  },
  setup() {
    return {};
  },
  //<h1>{{this.$route.name}}</h1>
};
</script>
